body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
	text-align: center;
}

.title{
	color: #00000094;
}

.form-style{
	box-shadow:  4px 3px 10px 1px #00000042;
	display: flex;
    align-content: center;
    /* align-items: center; */
    justify-content: space-around;
    flex-direction: column;
    max-width: 280px;
    margin: 0 auto;
	padding: 20px 20px 10px 20px;
	height: 100px;
}

.submit-btn{
	height: 26px;
    border-style: none;
    color: white;
    background-color: #2a4ca2;
	box-shadow:  4px 3px 10px 1px #00000042;
}

.submit-btn:hover{
	cursor: pointer;
	background-color: rgb(58, 106, 228);
}

.submit-btn:focus{
	outline: none;
}

.submit-btn:disabled{
	background-color: grey;
	opacity: 0.3;
	box-shadow: none;
	cursor: not-allowed;
}

.message{
	margin: 0px;
	padding: 0px;
	text-align: right;
	font-size: 0.8em;
}